<template>
  <div class="card">
    <div class="card-header">Exam02TemplateRefs</div>
    <div class="card-body">
      <div>
        <h6>[DOM 요소 참조]</h6>
        <p>data:{{ data }}</p>
        <button @click="setInputValue" class="btn btn-info btn-sm mr-2">값 변경</button>
        <input type="text" ref="inputTag" name="mid" />
      </div>
      <hr />
      <div>
        <h6>[자식 컴포넌트 참조]</h6>
        <p>child data:{{(childRef !== null)?childRef.data:""}}</p>
        <p>
          <button @click="changeChildData" class="btn btn-info btn-sm mr-2">자식 데이터 변경</button>
          <button @click="changeChildMethod" class="btn btn-info btn-sm mr-2">자식 메소드 호출</button>
          <!--쓰레기 코드
        자식 컴포넌트를 직접 들어가서 데이터 전달,수정하면 안됨  Prop으로 내려서 주거나, Event로 B에서 A로 올려주거나-->
        </p>
        <Child ref="childRef" />
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from "vue";
import Child from "./Child.vue";
const inputTag = ref(null);
const data = ref("summer");
const childRef = ref(null);
onMounted(() => {
  inputTag.value.focus();
  console.log(childRef);
});

function setInputValue() {
  //Event처리할때는 전혀 지장없이 쓸수있는게 DOM이 생성되고 버튼이벤트니까
  inputTag.value.value = data.value;
}
function changeChildData() {
  //가장 지양해야한다
  childRef.value.data += 1;
}
function callChildMethod() {
  childRef.value.method();
}
</script>

<style scoped></style>
